@import "variables";

.logo {
    border-radius: 20px;
}
.info.date{
    background-color: white;
    border-radius: 3px;
    text-align: center;
}
.leaflet-container{
    cursor: default;
}
.leaflet-left {
    width: 100%;
}
.leaflet-right{
    font-size: 16px;
    margin-right: 25px;
}
.control-left,
.control-right{
    width: 50%;
}
.control-left{
    float: left;
    .control-box{
        float: right;
    }
}
.control-right{
    float: right;
    .control-box{
        float: left;
    }
}
.control-box{
    width: 280px;
    margin-bottom: 20px;
}
.control-head {
    div.icon{
        width: 32px;
        height: 32px;
        float: left;
        padding-right: 3px;
        background-repeat: no-repeat;
    }
}
.control-box-rain{
    margin-right: 20%;
    .control-head div.icon{
        background-image: imageUser($rain-white-blue);
    }
}
.control-box-waterlevel {
    margin-right: 28%;
    .control-head div.icon{
        background-image: imageUser($waterlevel-white-blue);
    }
}
.control-box-waterquality {
    margin-right: 28%;
    .control-head div.icon{
        background-image: imageUser($waterquality-white-blue);
    }
}
.control-box-dam{
    margin-right: 34%;
    .control-head div.icon{
        background-image: imageUser($dam-white-blue);
    }
}
.control-box-storm{
    margin-left: 35%;
}
.control-box-storm .control-head div.icon{
    background-image: imageUser($storm-white-blue);
}
.control-box-clock{
    margin-left: 43%;
    .control-head div.icon{
        background-image: imageUser($clock-white-blue);
    }
}
.control-box-wave{
    margin-left: 46%;
    .control-head div.icon{
        background-image: imageUser($wave-white-blue);
    }
}
.control-box-warning{
    margin-left: 30%;
    .control-head div.icon{
        background-image: imageUser($warning-white-blue);
    }
}
